@import url(https://fonts.googleapis.com/css?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css?family=Oswald:200,300,400&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css?family=Oswald:200,300,400&display=swap);
*{
 margin: 0;
 padding: 0;
}
html{
  font-size: 16px;
}
body {
  margin: 0;
  font-family: 'Oswald', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  text-decoration: none; }

.container-logo {
  display: flex;
  font-family: 'Lobster', cursive;
  background-color: #0C2D48;
  flex-direction: row-reverse;
  height: 60px;
  align-items: center; }
  .container-logo li {
    list-style: none; }
  .container-logo__title {
    color: #ffffff;
    margin-right: 20px;
    font-size: 1.2rem; }
  .container-logo__logo {
    float: right;
    margin-top: 30px;
    margin-right: 20px;
    box-shadow: 0px 8px 10px -1px #a3a3a3;
    border-radius: 55%; }

.container-nav {
  font-family: 'Oswald', sans-serif;
  background: #D6E3EB;
  width: 100%;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(245, 246, 252, 0.12)), to(#0c2d48)), url(/static/media/home.fe89dd34.jpg);
  background-image: -webkit-linear-gradient(top, rgba(245, 246, 252, 0.12), #0c2d48), url(/static/media/home.fe89dd34.jpg);
  background-image: linear-gradient(to bottom, rgba(245, 246, 252, 0.12), #0c2d48), url(/static/media/home.fe89dd34.jpg);
  background-repeat: no-repeat;
  background-color: #0C2D48;
  height: 600px;
  background-size: cover; }
  .container-nav__item {
    display: inline-flex;
    flex-direction: row;
    color: #ffffff;
    list-style: none;
    margin-top: 500px; }
    .container-nav__item--logo {
      display: none; }
    .container-nav__item--title {
      display: none; }
    .container-nav__item--enter {
      max-width: 100px;
      margin-top: 30px;
      margin-left: 30px;
      border: 1px solid #ffffff;
      padding: 10px 40px 10px 40px;
      color: #ffffff;
      border-radius: 20px;
      text-transform: capitalize; }
    .container-nav__item--service {
      display: none; }
    .container-nav__item--register {
      margin-left: 10px;
      max-width: 100px;
      border: 1px solid #ffffff;
      padding: 10px 40px 10px 40px;
      color: #ffffff;
      border-radius: 20px;
      text-transform: capitalize; }

.container-alert {
  background: #0C2D48;
  height: 60px;
  display: flex; }
  .container-alert__button {
    background: #B1D4E0;
    font-size: 0.9rem;
    text-align: center;
    border-radius: 20px;
    font-weight: 300;
    width: 60%;
    margin: auto;
    padding: 5px; }

@media only screen and (min-width: 600px) {
  body {
    background: red; } }

@media only screen and (min-width: 968px) {
  body {
    background: green; } }

.container .container-menu {
  display: none; }

.container .container-main {
  background: #b1d4e0;
  background: -webkit-gradient(linear, left bottom, left top, from(#b1d4e0), to(white));
  background: -webkit-linear-gradient(bottom, #b1d4e0 0%, white 100%);
  background: linear-gradient(0deg, #b1d4e0 0%, white 100%); }
  .container .container-main__h2 {
    display: none; }
  .container .container-main__item {
    background: #e9e8e8;
    width: 260px;
    height: 70px;
    margin-top: 20px;
    margin-left: auto;
    margin-right: auto;
    list-style: none;
    border-radius: 20px;
    display: flex;
    color: #0C2D48;
    box-shadow: 0px 4px 10px -1px #a3a3a3; }
    .container .container-main__item p {
      font-family: 'Lobster', cursive;
      width: 100px;
      display: inline-block;
      font-size: 1.1rem;
      padding: 10px 10px 0px 20px; }
    .container .container-main__item span {
      font-family: 'Oswald', sans-serif;
      font-size: 0.7rem; }
    .container .container-main__item--selection {
      margin-top: 20px;
      margin-left: 60px; }

.container .show-card {
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
  background: #ffffff;
  color: #0C2D48;
  width: 220px;
  height: auto;
  border-radius: 20px;
  padding: 5%; }
  .container .show-card__title {
    font-family: 'Lobster', cursive;
    color: #0C2D48; }
    .container .show-card__title span {
      display: none; }
  .container .show-card__text {
    margin-top: 10px;
    font-weight: 300; }
  .container .show-card__dos {
    margin-top: 30px;
    text-align: center; }
    .container .show-card__dos--logo {
      display: none; }
    .container .show-card__dos--button {
      margin-top: 20px;
      padding: 5px;
      background: #0C2D48;
      color: #28C628;
      border-radius: 20px; }

.container .sets {
  display: none; }

.App {
  width: 100%;
}

